//   put  delete

export const putRequest = (url, params) => {
  return Vue.axios({
    method: 'put',
    url: `${local}${url}`,
    data: qs.stringify(params)
  });
};

export const deleteRequest = (url, params) => {
  return Vue.axios({
    method: 'delete',
    url: `${local}${url}`,
    data: {},
    params: params
  });
};



